/* Portfolio Animation */
.logo-container{
    margin-top: 150px;
    padding: 50px 0;
    display:flex;
    flex-direction:row;
    position: relative;
    overflow: hidden;
    font-family: 'Kumbh Sans', sans-serif;
}

svg#diagonal-line{
    stroke: white;
    fill: transparent;
    -webkit-animation-name: diag;
            animation-name: diag;
    -webkit-animation-duration: 0.55s;
            animation-duration: 0.55s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.port-container{
    font-weight: bold;
    overflow: hidden;
    font-size: 5rem;
    color: #AB7648;
    position: absolute;
    top: 180px;
    left: 120px;
    -webkit-animation: 0.45s linear slide-right;
            animation: 0.45s linear slide-right;
}

.folio-container{
    font-weight: bold;   
    font-size: 5rem;
    color: #121C26;
    position: absolute;
    top: 280px;
    left: 120px;
    -webkit-animation: 0.45s linear slide-left;
            animation: 0.45s linear slide-left;
}

@-webkit-keyframes diag{
    from{transform: rotate(0deg);}
    to{transform: rotate(90deg);}
}

@keyframes diag{
    from{transform: rotate(0deg);}
    to{transform: rotate(90deg);}
}

@-webkit-keyframes slide-right{
    from{
        margin-left: -100%;

    }
    to{
        margin-left: 0%;
    }
}

@keyframes slide-right{
    from{
        margin-left: -100%;

    }
    to{
        margin-left: 0%;
    }
}

@-webkit-keyframes slide-left{
    from{
        margin-left: 100%;

    }
    to{
        margin-left: 0%;
    }
}

@keyframes slide-left{
    from{
        margin-left: 100%;

    }
    to{
        margin-left: 0%;
    }
}


